.worksContainer{
    flex-direction: column;
    justify-content: flex-start;
}
.title{
    background-color: #c0fafa;
}
.works{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-sizing: border-box;
}